import { request } from '../request.js'
export default {
	data() {
		return {
			text: '',
			typeList: [],
			// 店上好货模拟数据
			onLineGoods: [],
			// 店下好货模拟数据
			downLineGoods: [
				{
					topic: '/static/images/product-02.png',
					title: '【给眼睛做个SPA】眼部按摩器升级',
					oldPrice: 99,
					newPrice: 199
				},
				{
					topic: '/static/images/product-01.png',
					title: '【给眼睛做个SPA】眼部按摩器升级',
					oldPrice: 99,
					newPrice: 199
				},
			],
			currentIndex: 0,
			cssText: {
				height: ''
			},
			id: '',
			// 请求分类
			page: 1,
			// 商品id
			category_id: '',
			// 控制否是执行上啦加载函数
			isflag:true,
			// 搜索内容
			searchText: ''
		};
	},
	methods: {
		//	监听搜索框输入的值
		handleSearch(event) {
			this.searchText = event.detail.value;
		},
		// 请求搜索数据
		searchData() {
			request({
				url: 'index.php?s=wap/goods/goodsSearchList',
				method: 'post',
				data: {
					sear_name: this.searchText
				}
			}).then(res => {
				if(res.data.code == 200) {
					// 提交到vuex做保存
					this.$store.commit('search',res.data);
					uni.navigateTo({
						url: '/pages/components/goods-result/goods-result'
					})
				}
			})
		},
		// 点击搜索
		search() {
			if(this.searchText == '') {
				uni.showToast({
					title: '请输入要搜索的产品',
					icon: 'none'
				})
				return
			}
			else {
				// 储存搜索历史
				this.$store.commit('saveKeywords',{keywords: this.searchText});
				this.searchData();
			}
		},
		handleClicks(item) {
			this.searchText = item;
			this.searchData();
		},
		// 实现点击切换tab
		handleClick(item,index) {
			this.page = 1;
			this.category_id = item.category_id
			this.currentIndex = index;
			// 实现点击滚动
			this.id = item.id;
			uni.showLoading({
				title: '加载中'
			});
			request({
				url: 'index.php?s=/wap/goods/listCategoryGoods',
				data: {
					cat_id: this.category_id,
					page: 1
				}
			}).then(res => {
				uni.hideLoading()
				// 处理图片
				let onLineGoods = res.data.list.data;
				for(let i = 0;i < onLineGoods.length;i++) {
					onLineGoods[i].pic_cover_micro = this.$api + onLineGoods[i].pic_cover_micro
				}
				this.onLineGoods = onLineGoods;
			})
		},
		// 监听右侧下拉触底
		handleBottom() {
			console.log(111);
			this.$nextTick(() =>{
				this.page = this.page + 1;
					uni.showLoading({
						title: '加载中'
					});
					request({
						url: 'index.php?s=/wap/goods/listCategoryGoods',
						data: {
							cat_id: this.category_id,
							page : this.page
						}
					}).then(res => {
						
						uni.hideLoading()
						if(res.data.list.page_count == this.page) {
							uni.showToast({
								title:'暂无更多数据',
								icon: 'none'
							})
							this.isflag = false;
							return
						}
						// 处理图片
						let onLineGoods = res.data.list.data;
						for(let i = 0;i < onLineGoods.length;i++) {
							onLineGoods[i].pic_cover_micro = this.$api + onLineGoods[i].pic_cover_micro
						}
						this.onLineGoods = [...this.onLineGoods,...onLineGoods];
					})
				})
			},
			handleDetail(id) {
				uni.navigateTo({
					url:'/pages/common/goods-detail/goods-detail?id='+id
				})
			}
		
	},
	onLoad() {
		// 获取可视区域高度
		uni.getSystemInfo({
			success:(res) => {
				this.cssText.height = res.windowHeight - 48 + 'px';
			}
		})
		// 左侧分类请求数据
		request({
			url: 'index.php?s=/wap/goods/goodsClassificationList'
		}).then(res => {
			for(let i = 0; i < res.data.list.length;i++) {
				res.data.list[i].id="item" + res.data.list[i].category_id
			}
			this.typeList = res.data.list;
			this.category_id = res.data.list[0].category_id;
			// this.requestGoodsList(this.category_id)
		})
	},
	onShow() {
		uni.getStorage({
			key: 'keywords',
			success: res => {
				this.$store.state.keywords = res.data
				this.list = res.data
			}
		})
	}
}
